import React, { useState, useEffect } from 'react'
import { getName, getUsername } from './modules/storageHelper'
import moment from 'moment'
import { Loading } from './Components'
import { Link } from 'react-router-dom'

function getGreetingTime() {
  const currentHour = moment().format('HH')

  if (currentHour >= 20 || currentHour <= 4)
    return 'Malam'
  else if (currentHour <= 10)
    return 'Pagi'
  else if (currentHour <= 15)
    return 'Siang'
  else
    return 'Sore'
}

function Home() {
  let name = getName() || getUsername() || 'Pengguna'
  let greeting = 'Selamat ' + getGreetingTime()

  const ep = 'https://jadwalsetda.herokuapp.com/api/events/'

  const [events, setEvents] = useState([])
  const [isLoading, setIsLoading] = useState(false)

  function getEvents() {
    const type = 'setda'
    const from = moment().startOf('day').utc().format('YYYY-MM-DD HH:mm')
    const to = moment().endOf('day').utc().format('YYYY-MM-DD HH:mm')
    const epx = ep + `?type=${type}&from=${from}&to=${to}`

    setIsLoading(true)
    fetch(epx)
      .then(res => res.json())
      .then(docs => {
        setIsLoading(false)
        setEvents(docs)
      })
      .catch(err => {
        setIsLoading(false)
        console.log(err)
      })
  }

  useEffect(() => {
    getEvents()
  }, [])

  return (
    <>
      <section className="hero is-info welcome is-small">
        <div className="hero-body">
          <div className="container">
            <h1 className="title">
              {greeting}, {name}!
          </h1>
            {/* <h2 className="subtitle">
            Lorem Ipsum dolor sit amet.
          </h2> */}
          </div>
        </div>
      </section>

      <div className="box">
        <h2 className="title">Setda Hari Ini</h2>

        {isLoading ? <Loading /> : null}

        {events.length ?
          <table className="table is-fullwidth is-striped">
            <thead>
              <tr>
                <th>Jam</th>
                <th>Acara</th>
              </tr>
            </thead>
            <tbody>
              {events.map(event => {
                return (
                  <tr key={event._id}>
                    <td>
                      {event.dateTime ?
                        moment(event.dateTime)
                          .local()
                          .format('HH:mm')
                        : null
                      }
                    </td>

                    {/* ACARA STARTS */}
                    <td>
                      <span className="has-text-weight-bold">{event.name}</span>
                      <br />
                      <div className="columns" style={{ textAlign: 'left' }}>
                        {event.place ?
                          <div className="column" style={{ padding: '.75rem 0' }}>
                            <span className="icon is-small"
                              style={{ marginRight: '.5rem', fontSize: '.875rem' }}
                            >
                              <i className="fas fa-map-marker-alt" />
                            </span>
                            <span className="is-small">{event.place}</span>
                          </div>
                          : null
                        }

                        {event.pic ?
                          <div className="column" style={{ padding: '.75rem 0' }}>
                            <span className="icon is-small"
                              style={{ marginRight: '.5rem', fontSize: '.875rem' }}
                            >
                              <i className="fas fa-briefcase" />
                            </span>
                            <span className="is-small">
                              {event.pic}
                              {event.participant ?
                                ` (${event.participant})`
                                : null
                              }
                            </span>
                          </div>
                          : null
                        }
                      </div>
                    </td>
                  </tr>
                )
              })}
            </tbody>
          </table>
          : null
        }

        {isLoading ?
          null
          : < Link to="/events/setda">Selengkapnya...</Link>
        }
      </div>
    </>
  )
}

export default Home
